.formInput {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    color: white;
    margin-top: 10px;
    overflow: hidden;

}

.formInput input,
textarea {
    border-radius: 5px;
    outline: 0;
    margin-top: 10px;
    width: 100%;
    height: 50px;
    padding: 5px;

}


.btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0px;

}


.formContainer {

    background-color: #42484d;
    width: 500px;
    padding: 20px;
    border-radius: 5px;

}

.addbtn {
    padding: 10px 20px;
    border-radius: 5px;
    background-color: #00c46a;
    border: none;
    font-size: 18px;
    cursor: pointer;

}

.backbtn {

    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    font-size: 18px;
    cursor: pointer;


}